<template>
  <div v-loading="loading">
    <cms-breadcrumb
      to="/region/list"
      type="list"
      :params="{areaType:$route.query.areaType}"
      @click="rest"
    >
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="上级区域" prop="parentId">
          <el-cascader
            filterable
            :options="options"
            :props="props"
            @change="getparentId"
            :change-on-select="true"
            v-model="formData.parentIds"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="区域编号" prop="areaCode" :error="errorMessage.areaCode">
          <el-input v-model="formData.areaCode" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="区域名称" prop="areaName" :error="errorMessage.areaName">
          <el-input v-model="formData.areaName" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="区域类型" prop="areaDictCode" :error="errorMessage.areaDictCode">
          <el-radio-group v-model="formData.areaDictCode">
            <el-radio :label="1">省/直辖市</el-radio>
            <el-radio :label="2">地市</el-radio>
            <el-radio :label="3">区</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="排序" prop="sortNum">
          <el-input v-model.number="formData.sortNum" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input
            type="textarea"
            :rows="5"
            placeholder="请输入内容"
            v-model="formData.remark"
            maxlength="100"
          ></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(true)" v-color>保存</cms-button>
        <cms-button type="rest" @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import {
  areaTreeArea,
  areaRegionSave,
  areaRegionGetAreaCode
} from '@/api/setting/area.js'

var defaultData = {
  parentIds: [0], // 根目录值
  parentId: '',
  areaCode: '',
  sortNum: 10,
  areaDictCode: 1,
  areaName: ''
}
export default {
  name: 'regionSave',
  mixins: [form],
  data() {
    var ischeckRouting = (rule, value, callback) => { // 验证区域code唯一
      areaRegionGetAreaCode({
        areaCode: this.formData.areaCode,
        id: ''
      }).then(res => {
        if (res.data) {
          callback(new Error('区域编号已存在'))
        } else {
          callback()
        }
      })
    }
    return {
      options: [],
      props: {
        value: 'id',
        label: 'areaName',
        children: 'childs'
      },
      formData: Object.assign({}, defaultData),
      rules: {
        parentIds: [rules.required('请选择上级区域', 'change')],
        areaName: [rules.required(), rules.len(1, 20)],
        areaCode: [rules.required(), rules.len(1, 10), rules.number(), rules.custom(ischeckRouting)],
        areaDictCode: [rules.required('请选择区域类型', 'change')],
        sortNum: [rules.number(), rules.required()]
      }
    }
  },
  computed: {
    areaData() {
      return this.$store.state.dict.area
    }
  },
  methods: {
    getparentId(arr) {
      // 获取最后一项id
      this.formData.parentId = arr[arr.length - 1]
    },
    setShow(value) {
      if (value === 2) {
        this.formData.is_show = false
      }
    },
    getMenuTree() {
      if (this.areaData.length > 0) {
        const root = [{ id: 0, areaName: '全国' }]
        this.options = root.concat(this.areaData)
      } else {
        areaTreeArea().then(res => {
          const root = [{ id: 0, areaName: '全国' }]
          this.options = root.concat(res.data)
        })
      }
    },
    save(state) {
      this.$refs['form'].validate(valid => {
        if (valid) {
          this.loading = true
          const params = Object.assign({}, this.formData)
          params.parentId = this.formData.parentIds[
            this.formData.parentIds.length - 1
          ] // 最后一层是他父级id
          delete params.parentIds
          if (params.parentId === 0) {
            params.parentId = ''
          }
          areaRegionSave(params)
            .then(res => {
              this.loading = false
              if (res.code === 200) {
                if (state) {
                  this.rest()
                  this.$routerLink('/region/list', 'list', {
                    areaType: this.formData.areaType
                  })
                  this.$successMessage('新增成功')
                } else {
                  this.rest()
                }
              } else {
                this.$errorMessage(res.message)
              }
              this.getMenuTree() // 刷新下栏目树
            })
            .catch(() => {
              this.loading = false
            })
        }
      })
    },
    reset() {
      this.formData = Object.assign({}, defaultData)
      this.$refs.form.clearValidate()
    }
  },
  created() {
    this.getMenuTree()
  }
}
</script>

<style>
</style>



